import { Injectable } from '@angular/core';

@Injectable()
export class ValidateService {

  constructor() { }

  validatePlayer(player, adminKey){
    if(player.name == undefined || adminKey != "220483"){
      return false;
    } else {
      return true;
    }
  }

  validateResult(adminKey){
    if(adminKey != "220483"){
      return false;
    } else {
        return true;
    }
  }
}



