import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService }
 from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name: String;
  adminKey: String;

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
    const player = {
      name: this.name,
      gamesPlayed: 0,
      points: 0,
    }

    const adminKey = this.adminKey;

    if(!this.validateService.validatePlayer(player, adminKey)){
      this.flashMessage.show('Player could not be added.', { cssClass: 'alert-danger', timeout: 10000 });
    }

    if(this.validateService.validatePlayer(player, adminKey)){
      // Register Player
      this.authService.registerPlayer(player).subscribe(data => {
        if(data.success){
          this.flashMessage.show('Player ' + player.name + ' added.', { cssClass: 'alert-success', timeout: 10000 });
          this.router.navigate(['/']);
        } else {
            this.flashMessage.show('Player could not be added', { cssClass: 'alert-danger', timeout: 10000 });
            this.router.navigate(['/register']);
          }
      });
    }
  }

}



