import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService }
 from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-results',
  templateUrl: './results.component.html',
  styleUrls: ['./results.component.css']
})
export class ResultsComponent implements OnInit {

  id: String;
  adminKey: String;
  results: Object;
  points: Number;
  gameNum: Number;
  place: Number;
  rebuys: Number;
  addons: Number; 

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onResultsSubmit(){
    const player = {
      gameNum: this.gameNum,
      place: this.place,
      points: this.points,
      rebuys: this.rebuys,
      addons: this.addons
    }

    const adminKey = this.adminKey

    if(!this.validateService.validateResult(adminKey)){
      this.flashMessage.show('Player could not be added.', { cssClass: 'alert-danger', timeout: 10000 });
    }

    // if(this.validateService.validateResults(adminKey)){
      // Add Results Player
      this.authService.registerResults(player, this.id).subscribe(data => {
        this.flashMessage.show('Player results added.', { cssClass: 'alert-success', timeout: 10000 });
        this.router.navigate(['/']);
      });
    // }
  }

}



