import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {
  player: any;
  authToken: any;
  result: any;
  id: any;
  final: any;

  constructor( private http: Http) { }

  registerPlayer(player){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('https://bridgepoker.herokuapp.com/players/register', player, { headers: headers }).map(res => res.json());
  }

  getPlayers(){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('https://bridgepoker.herokuapp.com/players', { headers: headers }).map(res => res.json());
  }

  registerResults(results, id){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.put('https://bridgepoker.herokuapp.com/players/player/' + id, results, { headers: headers }).map(res => res.json());
  }

  getPlayer(id){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('https://bridgepoker.herokuapp.com/players/player/' + id, { headers: headers }).map(res => res.json());
  }

  getFinal(id){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('https://bridgepoker.herokuapp.com/finals/final/' + id, { headers: headers }).map(res => res.json());
  }
}



